import UniTag from '@/components/uni-tag/uni-tag.vue'
export default {
	components: {
		UniTag,
	},
	data() {
		return {
			curry: 0,
			constData: {},

			id: '',
			titleText: 'Tony',
			titleTime: '',
			type: 3,
			color: '',

			fixedBox: '',
			fixeTag: '',
			windowHeight: '',

			list: [],
			tagList: [{
					name: '全部',
					type: 2
				},
				{
					name: '免费',
					type: 0
				},
				{
					name: '付费',
					type: 1
				}
			],

		}
	},
	methods: {
		//路由跳转
		navigator(list) {
			let url = ''
			if (list.type == this.$constData.contentType[0].key || list.type == this.$constData.contentType[2].key) {
				url = 'details'
			} else if (list.type == this.$constData.contentType[1].key) {
				url = 'detailsVideo'
			}
			console.log(`/pages/vip/column/${url}/${url}?id=${list.id}&id1=${list._id}`)
			uni.navigateTo({
				url: `/pages/vip/column/${url}/${url}?id=${list.id}&id1=${list._id}`
			})
		},

		// 从专栏id获取课程列表
		getContentByChannelId() {

			let cnt = {
				module: 'kkqt', // String 隶属
				channelId: this.id, // Long 专栏id
				status: 4, // Byte 专栏状态
				count: 10, // Integer 
				offset: 0, // Integer 
			}

			this.$api.getContentByChannelId(cnt, (res) => {
				if (res.data.rc == this.$util.RC.SUCCESS) {
					this.list = JSON.parse(res.data.c).list
					console.log(this.list)
				} else {
					console.log('error')
				}
			})
		},

		changeContent(e, f) {
			this.curry = e
			if (f == 2) {
				let cnt = {
					module: 'kkqt', // String 隶属
					channelId: this.id, // Long 专栏id
					status: 4, // Byte 专栏状态
					count: 10, // Integer 
					offset: 0, // Integer 
				}

				this.$api.getContentByChannelId(cnt, (res) => {
					if (res.data.rc == this.$util.RC.SUCCESS) {
						this.list = JSON.parse(res.data.c).list
						console.log(this.list)
					} else {
						console.log('error')
					}
				})
			} else {
				let cnt = {
					module: 'kkqt', // String 隶属
					channelId: this.id, // Long 专栏id
					status: 4, // Byte 专栏状态
					paid: f, // Byte 是否付费
					count: 10, // Integer 
					offset: 0, // Integer 
				}

				this.$api.getContentByChannelId(cnt, (res) => {
					if (res.data.rc == this.$util.RC.SUCCESS) {
						this.list = JSON.parse(res.data.c).list
						console.log(this.list)
					} else {
						console.log('error')
					}
				})
			}
		},
		
		// 将时间格式化
		changeTime(e){
			var t = parseInt(e)
			var a = new Date(t)
			var y = a.getFullYear()
			var m = 1 + a.getMonth()
			var d = a.getDate()
			var time = y + '年' + m + '月' + d + '日'
			this.titleTime = time
		}
	},



	onPageScroll: function() {
		// 顶部跟随
		var _this = this;
		uni.createSelectorQuery()
			.select('#salyt')
			.boundingClientRect(function(rects) {
				if (rects.top <= 40) {
					_this.fixedBox = 'position:fixed;top:0;z-index:1;margin-top:' + _this.windowHeight + 'px ';
					_this.fixeTag = 'position:fixed;top:10px;z-index:2;'
				} else if (rects.top > 1) {
					_this.fixedBox = '';
					_this.fixeTag = ''
				}
			})

			.exec();
	},


	onLoad(options) {
		this.constData = this.$constData
		console.log(options)
		this.id = options.id
		this.titleText = options.title
		this.color = options.color
		this.changeTime(options.time)
		
	},

	mounted() {

		// 获取屏幕高度
		let _this = this
		uni.getSystemInfo({
			success: function(res) {
				console.log(res.windowHeight);
				_this.windowHeight = -0.15 * res.windowHeight + 10
			}
		});

		this.getContentByChannelId()
	},


}
